*{
  margin: 0;
}
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
    margin: 0;
}
.app_component{
    display: flex;
}


    

.explorePage{
    flex: 0.8 1;
    background-color: #f9f9f9;
    padding: 40px 20px;
    display: flex;
    flex-wrap: wrap;
    
    
}


.exploreCard{
    height: 116px;
    width: 210px;
    background-color: white;
    border-radius: 10px;
    display: flex;
    flex-direction: column;
    margin: 5px;
}
.exploreCard:hover{
    background-color: #dedbdb;
    cursor: pointer;
}
.explore-icon{
    padding: 10px;
    font-size: 30px;
}
.exploreTitle{
    font-size: 20px;
    font-weight: 500;
    padding: 10px;
}
.video-recommendation{
    flex: 0.8 1;
    background-color: #f9f9f9;
    padding: 40px 20px;
}
.videos{
    display: flex;
    flex-wrap: wrap;
}
.Video_card{
    margin-bottom: 40px;
    width: 270px;
}
.videocard_thumbnail{
    height: 140px;
    width: 250px;
}
.videocard_info{
    display: flex;
    margin: 0px 12px;
}
.videocard_profile{
    height: 36px;
    width: 60px;
    margin-top: 12px;
    margin-right: 12px;
    margin-left: 0;
}
.videocard_text{
    padding-right: 24px;
}
.videocard_title{
    margin-bottom: 6px;
    margin:12px 0 4px 0;
    font-size: 14px;
}
.videocard_text > p{
    color: gray;
    font-size: 14px;
}
.sidebar{
    flex: 0.2 1;
}
.sidebar > hr{
    height: 1px;
    border: 0;
    background-color: lightgray;
    margin: 10px 0px;
}
.navlink{
    text-decoration: none;
    color: black;
}

.sidebarrow{
    display: flex;
    align-items: center;
    padding: 10px 20px;
    margin-right: 15px;
    
}
.sidebarrow:hover{
    background-color: #f8f8f8;
    cursor: pointer;
}
.sidebarrow:hover > .siderow_icon{
    color: red;
}
.sidebarrow:hover > .siderow_title{
    font-weight: bold;
}
.sidebarrow::selection{
    background-color: rgb(121, 33, 33);
}
.siderow_title{
    margin-left: 24px;
    font-family: "Roboto", "Noto", sans-serif;
    font-size: 1.2rem;
    line-height: 2rem;  
   font-weight: 500; 
  
}
.siderow_icon{
    height: 24px;
    width: 24px;
   
}
.sidebarrow.selected{
    background-color: lightgray;
}
.sidebarrow.selected > .siderow_icon{
    color: red;
}
.sidebarrow.selected > .siderow_title{
    font-weight: bold;
}
.shorts-page{
    flex: 0.8 1;
    padding: 40px 20px;
  display: flex;
  justify-content: center;
  align-items: center;
}
.shorts-content{
    display: flex;
}
.shorts-card{
    position: relative;
    width: 320px;
    height: 545px;
    background-color: rgba(51, 38, 38, 0.699);
    border-radius: 10px;
}
.shorts-reaction{
    display: flex;
    flex-direction: column;
    justify-content: flex-end;
    font-size: 40px;
    color: grey;
    margin-left: 10px;
}
.sreactionicon{
    margin-bottom: 15px;
    cursor: pointer;
}
.subs_button{
    background-color: rgb(239, 21, 21);
    color: white;
    position: absolute;
    bottom: 0;
    right: 0;
    margin-bottom: 10px;
    margin-right: 10px;
    padding: 10px;
    border-radius: 3px;
    cursor: pointer;
    font-weight: bold;
    letter-spacing: 1px;
    border: none;
}
.header{
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 20px;
    position: -webkit-sticky;
    position: sticky;
    top: 0;
    background-color: white;
}
.header-logo{
    height: 30px;
    object-fit: contain;
    margin-left: 20px;
}
.menu_icon{
    height: 30px;
    width: 30px;
}
.header-left{
    display: flex;
    align-items: center;
    
}
.header-mid{
    position: relative;
    display: flex;
    align-items: center;
    border: 2px solid #dedbdb;
    width: 40%;
    height: 30px;
}
.header-mid input{
   max-width: 100%; 
   border: none;
   outline: none;
   text-align: inherit;
   flex: 1 1;
}
.search_btn{
    position: absolute;
    height: 30px;
    width: 50px !important;
    border-left: 1px solid #dedbdb;
    color: gray;
    background-color: #fafafa;
    bottom: 0;
    right: 1px;
}
.header-right{
    display: flex;
    align-items: center;
}
.header_icon{
    margin-left: 20px;
    height: 30px;
    width: 30px;

}
.searchPage{
    flex: 0.8 1;
    background-color: #f9f9f9;
    padding: 40px 20px;
}
.searchPage_filter{
    display: flex;
    align-items: center;
    color: #606060;
  
    
}
.searchPage_filter > h2{
    margin-left: 10px;
    font-size: 15px !important;
    
}
.searchPage > hr{
    height: 1px;
    border: 0;
    background-color: lightgray;
    margin-top: 10px;
    margin-bottom: 10px;
}
.ChannelRow{
    display: flex;
    align-items: center;
    width: 70%;
}
.channelRow_text{
    display: flex;
    flex-direction: column;
}
.channelRow_text > p{
    color: #606060;
    font-size: small !important;
}
.channelRow_text > h4{
    display: flex;
    align-items: center;
    justify-content: space-between;
}
.channelRow_logo{
    height: 120px !important;
    width: 120px !important;
    margin: 10px 60px;
    border-radius: 70px;

}

.SearchVideoRow{
    display: flex;
    margin-bottom: 30px;
    max-width: 700px;

}

.searchVideo_img{
    object-fit: contain;
    width: 246px;
}
.searchVideoRow_text{
    margin-left: 14px;

}
.searchVideoRow_headline{
    font-size: 12px;
    color: #606060;
}
.subsno{
    font-weight: bolder;
    color: rgb(84,157,211);
}
.search_subs{
    background-color: lightgray;
    padding: 2px;
}
